import React, { Component, Fragment } from 'react';

import { Card, Row, Table, Popconfirm, Col } from 'antd';

import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import BtnEdit from 'components/common/button/BtnEdit';
import BtnDelete from 'components/common/button/BtnDelete';

import { specializedTrainings } from 'dump/data';

class SpecializedTraining extends Component {
  constructor(props) {
    super(props);
    this.state = {};
    this.columns = [
      {
        title: 'Ngành đào tạo',
        dataIndex: 'trainingIndustryCode',
        key: 'trainingIndustryCode',
      },
      {
        title: 'Mã chuyên ngành',
        dataIndex: 'code',
        key: 'code',
      },
      {
        title: 'Tên chuyên ngành',
        dataIndex: 'name',
        key: 'name',
      },
      {
        title: 'Tên tiếng anh',
        dataIndex: 'nameEn',
        key: 'nameEn',
      },
      {
        title: 'Operation',
        dataIndex: 'operation',
        render: (text, record) =>
          specializedTrainings.length >= 1 ? (
            <Fragment>
              <BtnEdit href={`#/specializeTraining/form/edit/${record.code}`} />
              <Popconfirm title="Sure to delete?" onConfirm={() => this.handleDelete(record.key)}>
                <BtnDelete />
              </Popconfirm>
            </Fragment>
          ) : null,
      },
    ];
  }

  render() {
    return (
      <Card title="Danh sách chuyên ngành">
        <Row>
          <Col span={24}>
            <BtnAddNew href="#/specializeTraining/form/new" />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table bordered dataSource={specializedTrainings} columns={this.columns} />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <SpecializedTraining />
  </Page>
);



